.palette__container .myColor{text-transform:uppercase;font-family:"Poppins",sans-serif;font-size:1.25rem;outline:none;border:none;background:none;text-align:center;font-weight:bold}.palette__container .myColor:hover{cursor:pointer}.palette__container__item{position:relative;width:20vw;height:60vh}.palette__container__buttons{text-align:center;display:flex;flex-direction:column;align-items:center;justify-content:center;height:100%}.button{outline:none;background-color:transparent;border:none;font-family:"Poppins",sans-serif}.button:hover{cursor:pointer}.button--lock{margin-top:1.5rem;height:30px}.interactions{display:flex;flex-direction:column;align-items:center}.summary-values{text-transform:uppercase;font-family:"Poppins",sans-serif;font-size:1.25rem;width:50vw;outline:none;border:none;background:none;text-align:center;margin-top:2rem}.summary-values:hover{cursor:pointer}.button--refresh{color:white;font-size:2rem;font-weight:bold;background:linear-gradient(90deg, #6590FF 0%, #5C005E 100%);border-radius:10px;padding:.5rem;width:15rem;margin-top:2rem;transition:.3s}.button--refresh:active{transform:translateY(0.3rem)}.palette__container{position:relative;display:flex;flex-direction:row;width:100vw;text-align:center}.header{display:flex;flex-direction:row;justify-content:space-between;align-items:center;width:90%;margin:0 auto}.header h1{padding:.5rem}.button--info{color:white;font-size:1rem;height:2.5rem;padding:0 1rem;font-weight:bold;background:linear-gradient(90deg, #6590FF 0%, #5C005E 100%);border-radius:10px;transition:.3s}.button--info:active{transform:translateY(0.3rem)}.info{position:fixed;bottom:50%;left:50%;max-width:400px;min-width:300px;width:30%;box-shadow:0 0 10px gray;transform:translate(-50%, 50%);z-index:100;background-color:white;padding:1rem 1.5rem;border-radius:10px;text-align:left;list-style-position:inside}.info .button{position:absolute;top:.6rem;right:.6rem}.info .button--close{height:1rem;width:1rem}.info p{padding-top:.5rem}.info h3{padding-top:1rem;text-align:center}:root{box-sizing:border-box;font-size:1rem}*{margin:0;padding:0;box-sizing:inherit}body{font-family:"Poppins",sans-serif;color:#343434;overflow-x:hidden}.App{text-align:center}

